const Noodl = require("@noodl/noodl-sdk");
import React, { useRef, useEffect } from "react";
import { Stage, Container, Sprite, Text } from "@pixi/react";
import { Texture, BlurFilter, TextStyle, ColorMatrixFilter } from "pixi.js";

/**
 * PixiStage Component
 * - Root container for all PixiJS content
 */
function PixiStage(props) {
  const stageRef = useRef(null);

  useEffect(() => {
    if (!stageRef.current) {
      const backgroundColor = props.background
        ? parseInt(props.background.replace("#", "0x"))
        : 0x1099bb;

      stageRef.current = (
        <Stage
          key="pixi-stage"
          width={props.width || 800}
          height={props.height || 600}
          options={{ backgroundColor }}
        >
          {props.children}
        </Stage>
      );
    }
  }, []);

  return (
    <div
      style={{
        width: props.width || 800,
        height: props.height || 600,
        position: "relative",
        overflow: "hidden",
        background: props.background || "#1099bb",
        zIndex: 1000,
      }}
    >
      {stageRef.current}
    </div>
  );
}

/**
 * PixiContainer Component
 * - Container for grouping PixiJS elements
 */
function PixiContainer(props) {
  return (
    <Container x={props.x || 0} y={props.y || 0}>
      {props.children}
    </Container>
  );
}

/**
 * PixiSprite Component
 * - Displays an image texture
 */
function PixiSprite(props) {
  const texture = props.textureUrl 
    ? Texture.from(props.textureUrl)
    : Texture.from("https://pixijs.io/pixi-react/img/bunny.png");

  return (
    <Sprite
      texture={texture}
      x={props.x || 0}
      y={props.y || 0}
      anchor={props.anchor || 0.5}
      scale={props.scale || 1}
      filters={props.filters || []}
    />
  );
}

/**
 * PixiText Component
 * - Displays text with styling
 */
function PixiText(props) {
  return (
    <Text
      text={props.text || ""}
      anchor={props.anchor || 0.5}
      x={props.x || 0}
      y={props.y || 0}
      filters={props.filters || []}
      style={
        new TextStyle({
          align: props.align || "center",
          fill: props.fill || "0xffffff",
          fontSize: props.fontSize || 24,
          letterSpacing: props.letterSpacing || 0,
          dropShadow: props.dropShadow || false,
          dropShadowColor: props.dropShadowColor || "#000000",
          dropShadowDistance: props.dropShadowDistance || 4,
        })
      }
    />
  );
}

/**
 * Define Noodl Nodes
 */
const PixiStageNode = Noodl.defineReactNode({
  name: "PixiStage",
  category: "Pixi",
  getReactComponent() {
    return PixiStage;
  },
  inputs: {
    width: { type: "number", default: 800 },
    height: { type: "number", default: 600 },
    background: { type: "color", default: "#1099bb" },
  },
  outputs: {
    isRendered: { type: "boolean", default: false },
  },
  signals: {
    forceRender: function () {
      this.setOutputs({ isRendered: true });
    },
  },
});

const PixiContainerNode = Noodl.defineReactNode({
  name: "PixiContainer",
  category: "Pixi",
  getReactComponent() {
    return PixiContainer;
  },
  inputs: {
    x: { type: "number", default: 0 },
    y: { type: "number", default: 0 },
  },
  outputs: {
    isRendered: { type: "boolean", default: false },
  },
  signals: {
    forceRender: function () {
      this.setOutputs({ isRendered: true });
    },
  },
});

const PixiSpriteNode = Noodl.defineReactNode({
  name: "PixiSprite",
  category: "Pixi",
  getReactComponent() {
    return PixiSprite;
  },
  inputs: {
    x: { type: "number", default: 0 },
    y: { type: "number", default: 0 },
    textureUrl: { type: "string", default: "https://pixijs.io/pixi-react/img/bunny.png" },
    anchor: { type: "number", default: 0.5 },
    scale: { type: "number", default: 1 },
  },
  outputs: {
    isRendered: { type: "boolean", default: false },
  },
  signals: {
    forceRender: function () {
      this.setOutputs({ isRendered: true });
    },
  },
});

const PixiTextNode = Noodl.defineReactNode({
  name: "PixiText",
  category: "Pixi",
  getReactComponent() {
    return PixiText;
  },
  inputs: {
    text: { type: "string", default: "Hello World" },
    x: { type: "number", default: 0 },
    y: { type: "number", default: 0 },
    anchor: { type: "number", default: 0.5 },
    fontSize: { type: "number", default: 24 },
    fill: { type: "color", default: "#ffffff" },
    letterSpacing: { type: "number", default: 0 },
    dropShadow: { type: "boolean", default: false },
    dropShadowColor: { type: "color", default: "#000000" },
    dropShadowDistance: { type: "number", default: 4 },
  },
  outputs: {
    isRendered: { type: "boolean", default: false },
  },
  signals: {
    forceRender: function () {
      this.setOutputs({ isRendered: true });
    },
  },
});

/**
 * Register the module in Noodl
 */
Noodl.defineModule({
  reactNodes: [PixiStageNode, PixiContainerNode, PixiSpriteNode, PixiTextNode],
  nodes: [],
  setup() {
    console.log("🎉 PixiJS Nodes Successfully Loaded in Noodl");
  },
});

export default {
  PixiStageNode,
  PixiContainerNode,
  PixiSpriteNode,
  PixiTextNode,
};
